const initialState: IInitialState = {
  listItems: [],
  selected: [],
  priceCar: [],
  reviewsUser: reviewUser,
  popUpBackCall: '',
  brandList: [],
  location: '',
};

export default function render(state = initialState, action: IAction) {
  switch (action.type) {
    case 'ADD_LIST_ITEM':
      return {
        ...state,
        listItems: action.payload,
      };
    case 'ADD_CAR':
      const newArr = state.listItems.map((item) => {
        if (item.id === action.payload) {
          return { ...item, active: item.active !== 'false' ? 'false' : true };
        }
        return item;
      });
      return {
        ...state,
        listItems: newArr,
      };
    case 'ADD_LOCATION':
      return {
        ...state,
        location: action.payload,
      };
    case 'ADD_PRICE':
      return {
        ...state,
        priceCar: action.payload,
      };
    case 'POP_UP_BACK_CALL':
      return {
        ...state,
        popUpBackCall: action.payload,
      };
    case 'ADD_BRAND':
      return {
        ...state,
        brandList: action.payload,
      };
    default:
      return {
        ...state,
      };
  }
}
